.nav {
    &__list {
        display: flex;
        flex-wrap: wrap;
    }

    &__item {
        flex-grow: 1;
    }   

    &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 1rem;

        width: 100%;
        height: 100%;
    }

    @media(max-width: $tablet_width) {
        display: none;
    }
}